  /**
   * Saves the updated Compass Configuration JSON to the backend.
   * Uses the cov_compass_service with requestType 'saveCompassConfiguration'.
   * @param configJson - The configuration JSON object to save
   * @returns Observable<any> - Emits success response or throws error
   */
  saveCompassConfiguration(configJson: any): Observable<any> {
    return new Observable<any>(observer => {
      this.customService.load(
        {
          customScript: {
            script: [
              {
                name: 'cov_compass_service',
                run: 'pre',
                id: 'saveCompassConfiguration',
                parameters: {
                  requestType: 'saveCompassConfiguration',
                  requestData: JSON.stringify(configJson)
                }
              }
            ],
            clearPatientSource: true
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          try {
            const raw = this.customService.get('saveCompassConfiguration');
            // console.log('Save config response:', raw);
            
            // Check if response has the expected structure
            if (raw && raw.reply && raw.reply.statusData) {
              const status = raw.reply.statusData.status;
              
              if (status === 'S') {
                // Success
                observer.next(raw);
                observer.complete();
              } else if (status === 'F') {
                // Failure - extract error message from subeventstatus if available
                let errorMessage = 'Failed to save Compass Configuration.';
                
                if (raw.reply.statusData.subeventstatus && 
                    raw.reply.statusData.subeventstatus.length > 0) {
                  const subEvent = raw.reply.statusData.subeventstatus[0];
                  if (subEvent.operationstatus) {
                    errorMessage = `Save failed: ${subEvent.operationstatus}`;
                  }
                }
                
                observer.error(new Error(errorMessage));
              } else {
                // Unknown status
                observer.error(new Error(`Unknown response status: ${status}`));
              }
            } else {
              // Invalid response structure
              observer.error(new Error('Invalid response format from server'));
            }
          } catch (error) {
            // console.error('Save config error:', error);
            observer.error(new Error('Failed to save Compass Configuration.'));
          }
        }
      );
    });
  }

